import React, {useState} from 'react'
import Home from './pages/Home'
import NotFound from './pages/NotFound'
// import Success from './pages/Success'
// import Timetable from './pages/Timetable'
// import Form from './pages/FormPage'
import { languageContext } from './utils/context'
import { BrowserRouter, Route, Switch, Redirect  } from "react-router-dom";

function App() {
  const [loading, setLoading] = useState(true)
  const [isLanguage, setIsLanguage] = useState({
    state: false,
    currentLanguage: 'Укр',
    languages: ['Рус', 'En'],
    allLanguages: ['Укр', 'Рус', 'En']
  });
  const changeLanguage = lang => {
    const fallbackLang = isLanguage.languages.map(item => {
        if(item === lang) {
            item = isLanguage.currentLanguage
        }
        return item
    })
    const newCurrentLanguage = lang
    // let newLanguage

    // switch (lang) {
    //     case 'Укр': newLanguage = 'ua'
    //         break;
    //     case 'Рус': newLanguage = 'ru'
    //         break;
    //     default: newLanguage = 'ua'
    //         break;
    // }

    setIsLanguage({...isLanguage, currentLanguage: newCurrentLanguage, languages:fallbackLang })
  }

  window.onresize = function() { 
    if (window.matchMedia("(min-width: 768px)").matches) {
      window.location.hash = ''
      window.location.reload();
      window.scrollTo(0, 0) 
    }
  }
  
  return (
    <languageContext.Provider value={{
      changeLanguage,
      isLanguage,
      setIsLanguage
    }}>
      <BrowserRouter>
        <Switch >
          <Route exact path='/' render={(props) => <Home {...props} loading={loading} setLoading={setLoading} />} />
          <Route path='/404' render={(props) => <NotFound {...props} setLoading={setLoading} />} />
          {/* <Route path='/success' render={(props) => <Success {...props} setLoading={setLoading} />} />
          <Route path='/timetable' render={(props) => <Timetable {...props} setLoading={setLoading} />} />
          <Route path='/send' render={(props) => <Form {...props} setLoading={setLoading} />} /> */}
          <Redirect to='/' />
        </Switch>
      </BrowserRouter>
    </languageContext.Provider>
  );
}

export default App;
